body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  text-align: center;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 800px;
  margin: auto;
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

header {
  background: #007bff;
  color: white;
  padding: 20px;
  border-radius: 10px 10px 0 0;
}

.search-box {
  margin: 20px 0;
}

.search-box input {
  padding: 10px;
  width: 70%;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.search-box button {
  padding: 10px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.product-list {
  display: flex;
  justify-content: space-around;
  margin: 20px 0;
}

.product-card {
  background: #fff;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 45%;
}

.product-card a {
  text-decoration: none;
  color: #007bff;
}

footer {
  background: #007bff;
  color: white;
  padding: 10px;
  border-radius: 0 0 10px 10px;
  margin-top: 20px;
}
